import React from 'react'
import Logo from './logo.png'
import { useState } from 'react'
import axios from 'axios';


const Main = () => {
    
    const [query, setQuery] = useState("");
    const [clientId, setClientId] = useState("xVDmcBwQXyHq_FI0fN3T74cM4-2Q8v6oR335cFphbS8");
    const [pics, setPics] = useState([]);
    
    const searchPhotos = async (e) => {
        e.preventDefault();
        const url = "https://api.unsplash.com/search/photos?page=1&query="+query+"&client_id="+clientId
        axios.get(url)
        .then((response) => {
            setPics(response.data.results);
        })
    };

    

    return (
        <div id="main" className="col-12 col-md-9 col-lg-10 mx-auto px-3">
            <div className="header py-3 sticky-top d-flex justify-content-between align-items-center flex-wrap col-12">
                {/* Logo */}
            <div className="small_screen_logo col-5 col-md-3 col-lg-2 d-block d-md-none">
                <img src={Logo} className="image-fluid col-10 logo" alt="bluecube logo" />
            </div>

            {/* Search bar */}
            <div className="col-10 d-none d-md-block">
                <form onSubmit={searchPhotos} className="col-12 px-2 py-2 d-flex align-items-center searchbar_container">
                    <div className="col-1 d-flex justify-content-center">
                        <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/></svg>
                    </div>
                    <input type="text" className="col-9 col-lg-10" value={query} onChange={(e) => setQuery(e.target.value)} placeholder="Find Something..." />
                    <button className="col-2 col-lg-1 py-1">Search</button>
                </form>
            </div>

            {/* Notification & User */}
            <div className="col-5 col-md-2 d-flex justify-content-end">
                <div className="noti px-3">
                <svg xmlns="http://www.w3.org/2000/svg" height="35px" viewBox="0 0 24 24" width="35px" fill="#444444"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M12 22c1.1 0 2-.9 2-2h-4c0 1.1.9 2 2 2zm6-6v-5c0-3.07-1.63-5.64-4.5-6.32V4c0-.83-.67-1.5-1.5-1.5s-1.5.67-1.5 1.5v.68C7.64 5.36 6 7.92 6 11v5l-2 2v1h16v-1l-2-2zm-2 1H8v-6c0-2.48 1.51-4.5 4-4.5s4 2.02 4 4.5v6zM7.58 4.08L6.15 2.65C3.75 4.48 2.17 7.3 2.03 10.5h2c.15-2.65 1.51-4.97 3.55-6.42zm12.39 6.42h2c-.15-3.2-1.73-6.02-4.12-7.85l-1.42 1.43c2.02 1.45 3.39 3.77 3.54 6.42z"/></svg>
                    <div className="noti_count d-flex align-items-center justify-content-center">3</div>
                </div>
                <div className="user d-flex">
                    <div>
                        <svg xmlns="http://www.w3.org/2000/svg" height="35px" viewBox="0 0 24 24" width="35px" fill="#444444"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zM7.07 18.28c.43-.9 3.05-1.78 4.93-1.78s4.51.88 4.93 1.78C15.57 19.36 13.86 20 12 20s-3.57-.64-4.93-1.72zm11.29-1.45c-1.43-1.74-4.9-2.33-6.36-2.33s-4.93.59-6.36 2.33C4.62 15.49 4 13.82 4 12c0-4.41 3.59-8 8-8s8 3.59 8 8c0 1.82-.62 3.49-1.64 4.83zM12 6c-1.94 0-3.5 1.56-3.5 3.5S10.06 13 12 13s3.5-1.56 3.5-3.5S13.94 6 12 6zm0 5c-.83 0-1.5-.67-1.5-1.5S11.17 8 12 8s1.5.67 1.5 1.5S12.83 11 12 11z"/></svg>
                        <div className="online p-2"></div>
                    </div>
                    <p className="d-none d-lg-block p-1">Abigail</p>
                </div>
            </div>
            
            {/* Search bar for small screen */}
            <div className="col-12 d-block d-md-none mt-3 sticky-top small_screen_search_bar">
                <form onSubmit={searchPhotos} className="col-12 px-2 py-2 d-flex align-items-center searchbar_container">
                    <div className="col-1 d-flex justify-content-center">
                        <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/></svg>
                    </div>
                    <input type="text" name="query" className="col-9 col-md-10" value={query} onChange={(e) => setQuery(e.target.value)} placeholder="Find Something..." />
                    <button type="submit" className="col-2 col-lg-1 py-1">Search</button>
                </form>
            </div>
            </div>
            <div className="pics_cont col-12">
                {pics.map((pic) => <div className="pic_holder col-12 p-2" key={pic.id}>
                <img className="card--image image-fluid col-12" alt={pic.alt_description} src={pic.urls.full} width="50%" height="50%" />
                </div> )}
            </div>
        </div>
    )
}

export default Main
